<template>
  <div class="smainn">
    <br />
    <div id="s4">
      <input type="text" class="text" value="" placeholder="Nhập họ tên" />
    </div>

    <div class="buttonhtmlcss">
      <button @click="html()" class="btn">
        <i class="fa fa-eye"></i> Xem HTML
      </button>
      <button @click="css()" class="btn">
        <i class="fa fa-eye"></i> Xem CSS
      </button>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    html() {
      var a = document.getElementById("s4").innerHTML;

      document.getElementById("thtml").innerHTML = a;
      document.getElementsByClassName("modal")[0].style.display = "block";
      
      document.getElementById("html").style.display = "block";
      document.getElementById("css").style.display = "none";
      // document.getElementsByTagName("style")[0].classList.add("abc");
    },
    css() {
      var b = document.getElementsByTagName("style")[4].innerHTML;
      document.getElementById("tcss").innerHTML = b;
      document.getElementsByClassName("modal")[0].style.display = "block";
      
      document.getElementById("html").style.display = "none";
      document.getElementById("css").style.display = "block";
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input.text {
  width: 90%;
  height: 50px;

  display: flex;
  margin: 0 auto;
  text-indent: 30px;
  border: none;
  background-color: #F5F1FF;
  border-radius: 10px;
  outline: none;
  transition: 0.4s ease-out;
}

input.text:focus {
  border-color: #ff0077;
  box-shadow: 0 0 0 5px rgba(255, 20, 145, 0.479);
}
</style>
